import { render } from 'react-dom';
import React, { Component, createElement } from 'react';
import $ from 'jquery'; 

var e = createElement;

class Table extends Component {
    constructor(props) {
        super(props);
        this.state = {$from: null, $to: null,from: null, to: null, sort: '', search: '', ...props.rows[0]};
    }
    componentDidMount(){
        let monthNames = ["Enero", "Febrero", "Marzo", "Abril", "Mayo", "Junio", "Julio", "Agosto", "Septiembre", "Octubre", "Noviembre", "Diciembre"];
        let dayNamesMin = ['DO', 'LU', 'MA', 'MI', 'JU', 'VI', 'SA'];
        let dateFormat = "dd/mm/yy";

        var $from = $('input#from-date').datepicker({monthNames, dayNamesMin, dateFormat}).on('change', (e) => this.from(e.target.value) );
        var $to = $('input#to-date').datepicker({monthNames, dayNamesMin, dateFormat}).on('change', (e) => this.to(e.target.value) );

        this.setState({ $from, $to });
    }
    render() {
        
        var rows = this.props.rows.filter(r => ((from , to, date) => (from ? date.getTime() >= from : true) && (to ? date.getTime() <= to : true) )(this.state.from, this.state.to, new Date(r.date.replace( /(\d{2})\/(\d{2})\/(\d{4})/, "$2/$1/$3")) )).filter(x => true).filter(row => this.props.cols.some(col => (row[col[1]] + '').toLowerCase().indexOf(this.state.search) != -1  )).sort(this.dynamicSort(this.state.sort));

        return (
            <div>
                <div class="booking">
                    <h1 class="booking__detail__title">Día {this.state.date} <small>Turno {this.state.time_shift}</small>   </h1>
                    <div class="booking__detail">
                        <h3 class="booking__detail__title">Ocupante: {this.state.name}</h3>
                    </div>
                    <div class="booking__occupant">
                        <h3 class="booking__detail__title">Invitados</h3>
                        {this.state.guests.length ? <ol>{this.state.guests.map((li, i) => <li><strong>{i+1}.</strong> {li}</li>)}</ol> : <u>No hay invitados registrados.</u>}
                    </div>   
                </div>
                <div className="booking__search">
                    <label class="form-group">
                        <span>Buscar :</span>
                        {e('input', {type: 'text', onChange: e => this.search(e)})}
                    </label>
                    <label class="form-group">
                        <span>Desde :</span>
                        {e('input', {id: 'from-date', type: 'text'})}
                    </label>
                    <label class="form-group">
                        <span>Hasta :</span>
                        {e('input', {id: 'to-date', type: 'text'})}
                    </label>
                </div>            
                <div className="table-responsive">
                    <table className="table table-hover">
                        <thead>
                            <tr>
                                {this.props.cols.map(col => e('th', {onClick :() => this.sort(col[1])}, col[0]))}
                            </tr>
                        </thead>
                        <tbody>
                            {rows.map((row, key) => e('tr', { key, class: this.state.id == row.id ? 'active' : null }, this.props.cols.map(col => e('td', {onClick: () => this.setState(row)}, row[col[1]]))))}
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }
    from(value) {
        var from = null;
        if(value){
            from = $.datepicker.parseDate( "dd/mm/yy", value );
            this.state.$to.datepicker('option', 'minDate' , from.addDays(1));
        }
        this.setState({from});
    }
    to(value) {
        var to = null;
        if(value){
            to = $.datepicker.parseDate( "dd/mm/yy", value );
            this.state.$from.datepicker('option', 'maxDate' , to.subDays(1));
        }
        this.setState({to});
    }

    search(){
        let search = event.target.value.trim().toLowerCase();
        this.setState({ search });
    }

    sort(a){
        var sort = this.state.sort == a ? '-' + a : a;
        this.setState({ sort }); 
    }
    
    dynamicSort(property) {
        var sortOrder = 1;

        if(property[0] === "-") {
            sortOrder = -1;
            property = property.substr(1);
        }

        return function (a,b) {
            if(sortOrder == -1){
                return (b[property]+ '').localeCompare(a[property]+ '');
            }else{
                return (a[property]+ '').localeCompare(b[property]+ '');
            }        
        }
    }
}

if(window['admin-lodge-booking-info']){
    var data = window['admin-lodge-booking-info'];
    var rows =  data.bookings;
    var cols = [['Día', 'date'], ['Turno', 'time_shift'], ['Nombre', 'name']];

    render(createElement(Table, {rows, cols}), document.querySelector('#lodge-info'));
}